import React, { memo, useMemo } from 'react';
import { COLORS, IRestProps, mergeRestProps } from '../../common';
import { renderReactNode } from '../../common';
import { SliderTip } from './SliderTip';
import { StyledSliderPoint } from './styled';

type PointProps = {
  // 以下为内部属性，PropsFromSlider
  handleMouseDownOnPoint?: () => void,
  point?: number,
  sliderLength?:number,
  isMove?: boolean,
  formatter?: Function,
  moveStart?:any,
  isStart?:boolean,
  disable?: boolean,
  tip?: React.ReactElement,

  // 以下为用户自定义属性
  tipBgColor?: string,
  tipTextColor?: string,
  pointBgColor?: string,
  pointBgColorDisabled?: string,
  pointBorderColor?: string,
  pointBorderColorDisabled?: string,
} & IRestProps ;

export const SliderPoint = memo((props: PointProps) => {
  const { handleMouseDownOnPoint, point = 0, sliderLength = 0, isMove, formatter, moveStart, isStart, disable, tip,
    pointBgColor = COLORS.WHITE,
    pointBgColorDisabled = '#D6D6D6',
    pointBorderColor = COLORS.WHITE,
    pointBorderColorDisabled = COLORS.LIGHT.LEVEL3_BASE_COLOR,
    style, ...rest } = props;
  const renderTip = useMemo(() => {
    const pointProps = { point, isMove, formatter };
    return renderReactNode(tip, <SliderTip {...pointProps} />, undefined);
  }, [tip,point, isMove, formatter]);

  const pointStyle = useMemo(() => {
    return {
      backgroundColor: disable ? pointBgColorDisabled : pointBgColor,
      border: `1px solid ${disable ? pointBorderColorDisabled : pointBorderColor}`,
      boxShadow: disable ? 'none' : `0 0 1px 1px ${COLORS.LIGHT.LEVEL3_BASE_COLOR}`,
      cursor: disable ? 'initial' : 'pointer',
      ...style,
      left: `${point / sliderLength * 100}%`,
    };
  }, [disable, point, style, sliderLength, pointBgColorDisabled, pointBgColor, pointBorderColor, pointBorderColorDisabled]);
  return (
    <StyledSliderPoint
      disable={disable}
      isMove={isMove}
      onMouseDown={() => {
        moveStart && (moveStart.current = isStart);
        handleMouseDownOnPoint?.();
      }}
      {...mergeRestProps({
        ...rest,
        style:pointStyle
      }, '')}
    >
      {renderTip}
    </StyledSliderPoint>
  );
});
